/*
*  HeaderNav
* 
*  
    menu: 
* 
*/
require('styles/HeaderNav.scss');

import React from 'react';
import {Link} from 'react-router';

export default class HeaderNav extends React.Component {
    constructor(props, context) {
        super(props, context)
        this.state = {
        }
    }


    render() {
		let showMenuStyle
		if(this.props.isShowMenu){
			showMenuStyle = {
				display: 'block'
			}
		}else{
			showMenuStyle = {
				display: 'none'
			};
		}
        return (
            <div className="header-nav" style={showMenuStyle}>
                { this.props.menu.map(function (data, step) {
                    return (
                        <Link key={step} to='list' title={data}>
                            <div className="list-item">
                                <img src={`../images/live/${step + 1}.jpg`} alt=""/>
                                <p>{data}</p>
                            </div>
                        </Link>
                    )
                })
                }
            </div>
        );
    }
}

HeaderNav.defaultProps = {
    menu: [
        '',
        'DOTA2',
        '',
        '',
        '',
        '',
        '',
        '',
        'Pokemon Go',
        '',
        '',
        '3'
    ]
}


