require('styles/Poster.scss');

import React from 'react';

const bg = require('../../images/1.jpg')
export default class Poster extends React.Component {
    constructor(props, context) {
        super(props, context)
        this.state = {
        }
    }

    render() {
        return (
            <div className="poster">
                <img src={bg} alt=""/>
                <div className="play-btn"></div>
                <div className="room-info">
                    <span className='name'>Miss</span>
                    <span className="class"></span>
                </div>
                <div className="room-stop-focus">
                    <p className="stop-title"></p>
                    <p className="stop-tip"></p>
                    <button className="stop-more hd-foucs"></button>
                </div>
            </div>
        );
    }
}



