/*
*  Login
* 
*/
require('styles/App.css');
require('styles/Login.scss');

import React from 'react';
import CM from '../actions/AppMethods';

export default class Login extends React.Component {
    constructor(props, context) {
        super(props, context)
        this.state = {
        }
    }

	handleLogin() {
		let msg = {
			type: 'error',
			title: '',
			content: ''
		}
		CM.alert(msg);
	}

	render() {
		return (
			<div className="logo">
				<div className="topbar"></div>
				<div className="login-from">
					<h1></h1>
					<div className="input-group">
						<label htmlFor="mobile"></label>
						<input id="mobile" name="mobile" type="text" ref="mobile" required/>
					</div>
					<div className="input-group">
						<label htmlFor="password"></label>
						<input id="password" name="password" type="text" ref="password" required/>
					</div>
					<div className="submit-field">
						<button type="submit" className="btn-large" onClick={this.handleLogin}></button>
					</div>
					<div className="tips">
						<p><a href="#"></a></p>
					</div>
				</div>
			</div>
		);
	}
}


